.movie_container {
    display: grid;
    grid-template-columns: 150px 400px;
    border: 4px dashed grey;
    border-radius: 10px;
    padding: 10px;
    background-color: white;
}

.image {
    width: 130px;
}

.genreBox {
    display: flex;
    
}

.genre {
    border: 2px solid rgb(123, 94, 183);
    border-radius: 10px;
    margin: 0 3px;
    padding: 0 3px;

}